* {
    box-sizing: border-box;
  }
  
  h1,
  h2,
  h3,
  p {
    margin: 0;
  }
  
  ul,
  li {
    padding: 0px;
    margin: 0px;
    list-style: none;
  }

  img {
    display: block;
    max-width: 100%;
    max-height: 100%;
  }
  
  button {
    cursor: pointer;
  }
  
  body {
    padding: 10px;
    margin: 0px;
    font-family: "Roboto", sans-serif;
    background-color: gray;
  }

  a{
    text-decoration: none;
    color: black;
  }

  
  .visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  .layout{
    display: flex;
    gap: 10px;
  }

  .container-layuot{
    border: 1px solid black;
    
    background-color: wheat;
    box-shadow: 1px 1px 5px black
  }

  .container{
    width: 1200px;
    padding-left: 30px;
    padding-right: 30px;
  }




  